//
//   Base Settings
//

// High-level settings for the entire site
// To create relational sizing and aesthetically pleasing design
$base-mobile-width: 960px;
$base-spacing-unit: 16px;
$base-font-size: 16px;
$base-line-height: $base-spacing-unit;

// Spacing sizes
$spacing-smallest: $base-spacing-unit / 2;
$spacing-smaller: $base-spacing-unit / 1.5;
$spacing-small: $base-spacing-unit / 1.2;
$spacing-normal: $base-spacing-unit;
$spacing-large: $base-spacing-unit * 1.2;
$spacing-larger: $base-spacing-unit * 1.5;
$spacing-largest: $base-spacing-unit * 2;

// Font sizes
$font-xsmall: $base-font-size / 1.5;
$font-small: $base-font-size / 1.2;
$font-normal: $base-font-size;
$font-large: $base-font-size * 1.2;
$font-larger: $base-font-size * 1.5;
$font-largest: $base-font-size * 2;

// Font weights
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 500;
$font-weight-black: 700;
$font-weight-heading: $font-weight-bold;

// Header sizes
$header-size-1: $base-font-size * 2;
$header-size-2: $base-font-size * 1.5;
$header-size-3: $base-font-size * 1.35;
$header-size-4: $base-font-size * 1.2;
$header-size-5: $base-font-size;
$header-size-6: $base-font-size * 0.9;